import React, { useState } from 'react';
import axios from 'axios';
import styles from './MapListStyles'; //   

const MapRequest = () => {
  const [ipAddress, setIpAddress] = useState('');
  const [mapData, setMapData] = useState(null);
  const [error, setError] = useState(null);

  const sendMapRequest = async () => {
    console.log("sendMapRequest called"); //   
    try {
      //    
      const header = [
        0x5A, 0x01, 0x00, 0x01, 
        0x00, 0x00, 0x00, 0x00,  //   0 (    )
        0x05, 0x14,              // API  1300 (  )
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00  // Reserved area
      ];
  
      //  16  
      const headerHex = header.map(byte => byte.toString(16).padStart(2, '0')).join('');
  
      // IP   
      const response = await axios.post(`http://${ipAddress}:19204/map-data`, { header: headerHex }, {
        timeout: 5000 // 5     
      });
  
      //  
      if (response.data.ret_code === 0) {
        setMapData(response.data);  //    
        setError(null);
      } else {
        setError('    .');
        setMapData(null);
      }
    } catch (err) {
      //    ( ,   )
      if (err.code === 'ECONNABORTED') {
        setError('  .   .');
      } else {
        setError('   .');
      }
      setMapData(null);
    }
  };
  
  

  return (
    <div style={styles.container}>
      <div style={styles.header}>Map List</div>
      <div style={styles.inputContainer}>
        <input
          type="text"
          value={ipAddress}
          onChange={(e) => setIpAddress(e.target.value)}
          placeholder="IP  "
          style={styles.input}
        />
        <button onClick={sendMapRequest} style={styles.button}></button>
      </div>
      <div style={styles.responseContainer}>
        {error && <p style={styles.error}>{error}</p>}
        {mapData && (
          <ul style={styles.mapList}>
            {mapData.maps.map((mapName, index) => (
              <li key={index} style={styles.mapItem}>{mapName}</li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default MapRequest;
