import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './TaskRobotPage.css';

const POLLING_INTERVAL = 5000; // 5  
const amr_ord_pri_ORDER = { H: 3, M: 2, L: 1 }; //   

const TaskRobotPage = () => {
  const [tasks, setTasks] = useState([]);
  const [robots, setRobots] = useState([]);
  const [newTask, setNewTask] = useState({
    amr_ord_no: "",
    amr_ord_flag: "N",
    amr_ord_ty: "S",
    wo_no: "",
    item_code: "",
    serial_no: "",
    tray_no: "",
    amr_ord_dtm: "",
    loc_no_to: "",
    loc_no_fr: "",
    amr_ord_pri: "L",
  });

  useEffect(() => {
    //   
    fetchTasks();

    //  
    const intervalId = setInterval(() => {
      fetchTasks();
    }, POLLING_INTERVAL);

    //     
    return () => clearInterval(intervalId);
  }, []);

  const fetchTasks = async () => {
    try {
      const response = await axios.get(`${process.env.REACT_APP_NODE_SERVER_IP}/SAM_IF004/TASKS`);
      console.log(response.data)
      setTasks(response.data.rows);
    } catch (error) {
      console.error('Error fetching tasks:', error.message);
    }
  };
  const handleDeleteTask = async (task) => {
    //    
    const cancelTask = {
      amr_ord_no: task.wo_no, //   (wo_no amr_ord_no  )
      amr_ord_flag: "Y", //  
    };
  
    try {
      console.log(' :', cancelTask);
      const response = await axios.post(`${process.env.REACT_APP_NODE_SERVER_IP}/SAM/TASK_INFORM_MESSAGE`, cancelTask, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      console.log('Task canceled successfully:', response.data);
      alert('  .');
  
      //     
      fetchTasks();
    } catch (error) {
      console.error('Error canceling task:', error.message);
      alert('  .');
    }
  };
  

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString(); //   
  };

  const handleSendTask = async (task) => {
    console.log('  :', task);

    try {
      const response = await axios.post(`${process.env.REACT_APP_NODE_SERVER_IP}/SEND_TASK`, task, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      console.log('Task sent successfully:', response.data);
      alert('  .');
    } catch (error) {
      console.error('Error sending task:', error.message);
      alert('  .');
    }
  };

  // const handleNewTaskSubmit = async (event) => {
  //   event.preventDefault();
  
  //   //     
  //   const currentTime = new Date().toISOString(); // ISO 8601 
  
  //   const taskWithCurrentTime = {
  //     ...newTask,
  //     amr_ord_dtm: currentTime, //     
  //   };
  
  //   console.log('  :', taskWithCurrentTime);
  
  //   try {
  //     const response = await axios.post(`${process.env.REACT_APP_NODE_SERVER_IP}/SAM/TASK_INFORM_MESSAGE`, taskWithCurrentTime, {
  //       headers: {
  //         'Content-Type': 'application/json',
  //       },
  //     });
  //     console.log('New task sent successfully:', response.data);
  //     alert('   .');
  
  //     //   
  //     setNewTask({
  //       amr_ord_no: "",
  //       amr_ord_flag: "N",
  //       amr_ord_ty: "Transfer",
  //       wo_no: "",
  //       item_code: "",
  //       serial_no: "",
  //       tray_no: "",
  //       loc_no_to: "",
  //       loc_no_fr: "",
  //       amr_ord_pri: "L",
  //     });
  
  //     fetchTasks(); //   
  //   } catch (error) {
  //     console.error('Error sending new task:', error.message);
  //     alert('   .');
  //   }
  // };
  const handleNewTaskSubmit = async (event) => {
    event.preventDefault();
  
    //     
    const currentTime = new Date().toISOString(); // ISO 8601 
  
    const taskWithCurrentTime = {
      ...newTask,
      amr_ord_dtm: currentTime, //     
    };
  
    console.log('  :', taskWithCurrentTime);
  
    try {
      const response = await axios.post(`${process.env.REACT_APP_NODE_SERVER_IP}/SAM_IF004`, taskWithCurrentTime, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      console.log('New task sent successfully:', response.data);
      alert('   .');
  
      //   
      setNewTask({
        amr_ord_no: "",
        amr_ord_flag: "N",
        amr_ord_ty: "S",
        wo_no: "",
        item_code: "",
        serial_no: "",
        tray_no: "",
        loc_no_to: "",
        loc_no_fr: "",
        amr_ord_pri: "L",
      });
  
      fetchTasks(); //   
    } catch (error) {
      console.error('Error sending new task:', error.message);
      alert('   .');
    }
  };
  const sortedTasks = [...tasks].sort((a, b) => {
    const amr_ord_priA = amr_ord_pri_ORDER[a.amr_ord_pri] || 0;
    const amr_ord_priB = amr_ord_pri_ORDER[b.amr_ord_pri] || 0;
  
    if (amr_ord_priA !== amr_ord_priB) {
      return amr_ord_priB - amr_ord_priA; //    
    }
    return new Date(a.created_at) - new Date(b.created_at); //     
  });
  

  return (
    <div className="page-container">
      <div className="section-container">
        <h2 className="section-title"> </h2>
        <div className="table-container">
          <div className="header-row">
            <div className="table-header-item"> </div>
            <div className="table-header-item"></div>
            <div className="table-header-item"></div>
            <div className="table-header-item"> </div>
            <div className="table-header-item"> </div>
          </div>
          {sortedTasks.map((task, index) => (
  <div key={index}>
    <div className="table-row">
      <div className="table-cell">{task.amr_ord_no}</div>
      <div className="table-cell">{task.status}</div>
      <div className="table-cell">{task.amr_ord_pri}</div>
      <div className="table-cell">{`${task.loc_no_fr}  ${task.loc_no_to}`}</div>
      <div className="table-cell">{formatTimestamp(task.amr_ord_dtm)}</div>
      <div className="table-cell">
        <button
          className="send-task-button"
          onClick={() => handleSendTask(task)} //   
        >
          
        </button>
        <button
          className="delete-task-button"
          onClick={() => handleDeleteTask(task)} //  
        >
          
        </button>
      </div>
    </div>
    {index !== sortedTasks.length - 1 && <div className="row-divider"></div>}
  </div>
))}

        </div>
      </div>

      {/*    */}
      <div className="new-task-form-container">
  <h2 className="new-task-form-title">  </h2>
  <form onSubmit={handleNewTaskSubmit} className="new-task-form">
    <div className="new-task-form-group">
      <label>  (amr_ord_no)</label>
      <input
        type="text"
        value={newTask.amr_ord_no}
        onChange={(e) => setNewTask({ ...newTask, amr_ord_no: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (amr_ord_flag)</label>
      <select
        value={newTask.amr_ord_flag}
        onChange={(e) => setNewTask({ ...newTask, amr_ord_flag: e.target.value })}
      >
        <option value="N">N</option>
        <option value="C">C</option>
      </select>
    </div>
    <div className="new-task-form-group">
      <label>  (amr_ord_ty)</label>
      <input
        type="text"
        value={newTask.amr_ord_ty}
        onChange={(e) => setNewTask({ ...newTask, amr_ord_ty: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>   (wo_no)</label>
      <input
        type="text"
        value={newTask.wo_no}
        onChange={(e) => setNewTask({ ...newTask, wo_no: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (item_code)</label>
      <input
        type="text"
        value={newTask.item_code}
        onChange={(e) => setNewTask({ ...newTask, item_code: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (serial_no)</label>
      <input
        type="text"
        value={newTask.serial_no}
        onChange={(e) => setNewTask({ ...newTask, serial_no: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (tray_no)</label>
      <input
        type="text"
        value={newTask.tray_no}
        onChange={(e) => setNewTask({ ...newTask, tray_no: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (loc_no_fr)</label>
      <input
        type="text"
        value={newTask.loc_no_fr}
        onChange={(e) => setNewTask({ ...newTask, loc_no_fr: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label>  (loc_no_to)</label>
      <input
        type="text"
        value={newTask.loc_no_to}
        onChange={(e) => setNewTask({ ...newTask, loc_no_to: e.target.value })}
        required
      />
    </div>
    <div className="new-task-form-group">
      <label> (amr_ord_pri)</label>
      <select
        value={newTask.amr_ord_pri}
        onChange={(e) => setNewTask({ ...newTask, amr_ord_pri: e.target.value })}
      >
        <option value="H">H</option>
        <option value="M">M</option>
        <option value="L">L</option>
      </select>
    </div>
    <button type="submit" className="add-task-button"> </button>
  </form>
</div>


    </div>
  );
};

export default TaskRobotPage;
